<template>
  <div class="mt-4">

     <!-- Modal Add Student -->
      <div
        class="modal fade"
        id="exampleModal1"
        tabindex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title text-center" id="exampleModalLabel">
                Add Student
              </h5>
              <button
                type="button"
                class="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <!-- modal body -->
            <div class="modal-body">
              <div class="card-body">
                <form @submit.prevent="addStudent">
                  <div class="form-group">
                    <label for="name">Name</label>
                    <input
                      type="text"
                      id="name"
                      name="name"
                      placeholder="Enter name"
                      v-model="name"
                      class="form-control"
                      required
                    />
                  </div>
                  <div class="form-group">
                    <label for="username">Username</label>
                    <input
                      type="text"
                      id="username"
                      name="username"
                      placeholder="Enter username"
                      v-model="username"
                      class="form-control"
                      required
                    />
                  </div>
                  <div class="form-group">
                    <label for="email">Email</label>
                    <input
                      type="text"
                      id="email"
                      name="email"
                      placeholder="Enter email"
                      v-model="email"
                      class="form-control"
                      required
                    />
                  </div>
                  <div class="form-group">
                    <label for="password">Password</label>
                    <input
                      type="password"
                      id="password"
                      name="password"
                      placeholder="Enter password"
                      v-model="password"
                      class="form-control"
                      required
                    />
                  </div>
                  <div class="form-group">
                    <label for="confirm_password">Confirm Password</label>
                    <input
                      type="password"
                      id="confirm_password"
                      name="confirm_password"
                      placeholder="Confirm Password"
                      v-model="confirm_password"
                      class="form-control"
                      required
                    />
                  </div>

                  <button class="btn btn-success mt-2 " data-bs-dismiss="modal">Add Student</button>
                    <button class="btn btn-success mt-2 ms-2" type="reset">Clear</button>
                </form>
              </div>
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>

  <!-- Modal Edit/Update User -->
      <div
        class="modal fade"
        id="exampleModal"
        tabindex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title text-center" id="exampleModalLabel">
                Edit Student
              </h5>
              <button
                type="button"
                class="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <!-- modal body -->
            <div class="modal-body">
              <div class="card-body">
                <form @submit.prevent="updateStudent">
                  <div class="form-group">
                    <label for="updatedName">Name</label>
                    <input
                      type="text"
                      id="updatedName"
                      name="name"
                      placeholder="Enter name"
                      v-model="updatedname"
                      class="form-control"
                    />
                  </div>
                  <!-- <div class="form-group">
                    <label for="username">Username</label>
                    <input
                      type="text"
                      id="username"
                      name="username"
                      placeholder="Enter username"
                      v-model="username"
                      class="form-control"
                    />
                  </div> -->
                  <!-- <div class="form-group">
                    <label for="updateEmail">Email</label>
                    <input
                      type="text"
                      id="updateEmail"
                      name="email"
                      placeholder="Enter email"
                      v-model="email"
                      class="form-control"
                    />
                  </div> -->
                  <!-- <div class="form-group">
                    <label for="password">Password</label>
                    <input
                      type="password"
                      id="password"
                      name="password"
                      placeholder="Enter password"
                      v-model="password"
                      class="form-control"
                    />
                  </div>
                  <div class="form-group">
                    <label for="confirm_password">Confirm Password</label>
                    <input
                      type="password"
                      id="confirm_password"
                      name="confirm_password"
                      placeholder="Confirm Password"
                      v-model="confirm_password"
                      class="form-control"
                    />
                  </div> -->
                  <!-- <button class="btn btn-success mt-2" @click="updateStudent(_id)">Update Student</button> -->
                  <button class="btn btn-success mt-2" data-bs-dismiss="modal">Update Student</button>

                </form>
              </div>
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>




    <button
      class="btn btn-success mb-5"
      data-bs-toggle="modal"
      data-bs-target="#exampleModal1"
    >
      Add Student 
    </button>
    <div class="table-responsive">
      <table class="table table-striped table-sm">
        <thead>
          <tr class="text-center fw-bold">
            <!-- <th scope="col">Id</th> -->
            <th scope="col">Name</th>
            <th scope="col">Username</th>
            <th scope="col">Email</th>
            <th scope="col">Update</th>
            <th scope="col">Delete</th>
          </tr>
        </thead>
        <tbody class="text-center">
          <tr class="mt-2">
          <tr class="mt-2" v-for="(student, _id) in students" :key="_id"> 
            <!-- <td>{{ student._id }}</td>  -->
            <td>{{ student.name }}</td>
            <td>{{ student.username }}</td>
            <td>{{ student.email }}</td>
          
            <td><button class="btn btn-primary" data-bs-toggle="modal"
      data-bs-target="#exampleModal" @click="editStudent(_id)">Update</button></td>
            <td><button class="btn btn-danger" @click="deleteStudent(_id)">Delete</button></td>
          </tr>
        </tbody>
      </table>

     
    


    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters} from "vuex";
import axios from "axios";
import config from "@/config"


export default {
  name: "AddStudent",

  data() {
    return {
      username: "",
      name: "",
      email: "",
      password: "",
      confirm_password: "",

        //students array
      students:[],
      

      updatedname:""
    };
  },
  methods:{
        //Add student
        ...mapActions(["addstudent", "getStudents"]),
    addStudent() {
      let student = {
        username: this.username,
        name: this.name,
        email: this.email,
        password: this.password,
        confirm_password: this.confirm_password,
        

      };
      this.addstudent(student)
     
      .then((res) => {

        if (res.data.success) {
          // this.router.push("add-student");
          this.$toasted.show('Student Added');
          this.getStudents()
        }
      });
    },
    
     //Get Students list
        async getStudents() {
      try {
        let {
          data
        } = await axios.get(`${config.apiBaseUrl}/api/users/get-student`)
       
        this.students = data
      } catch (error) {
        console.error(error)
      }

    },

    // Add data to edit form
    editStudent(_id){

        this.updatedname = this.students[_id].name
        this._id = this.students[_id]._id
        // this.email = this.students[_id].email
            },
    
    //delete student
     async deleteStudent(_id){
      
       let id  = this.students[_id]._id
      let res = await axios.delete(`${config.apiBaseUrl}/api/users/delete-student`,{data: { _id:id} })
  
  this.getStudents() //calling get student
        this.$toasted.show('Student Deleted');
      if(res.data.success){
          console.log('add something here')
        }
        return res;
     },

    //  update Student
     async updateStudent(){
          let student={
      name :this.updatedname,
       _id :this._id
       }
    
    let res = await axios.patch(`${config.apiBaseUrl}/api/users/update-student`,student)
      this.getStudents() //calling gets student
        this.$toasted.show('Student Updated');
      if(res.data.success){
          console.log('add something here')
        }
        return res;
     


    }


      },
  computed:{
    ...mapGetters(["user"]) //this is not changed
  },
  created(){
    this.getStudents()

    }
};
</script>

<style scoped>
label {
  font-weight: 600 !important;
  margin: 0.5em 0em 0.5em 0em;
}
</style>




